.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background: linear-gradient(45deg, lime, #2c3e50, #34495e, #16a085, #1abc9c, #8e44ad, #9b59b6, #2ecc71, lime);
  min-height: auto;
  display: flex;
  flex-direction: column;
  /* align-items: center;
  justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.team-one {
  height: 100%;
  width: 50%;
  background-color: transparent;
}

.team-two {
  height: 100%;
  width: 50%;
}

