.input {
  height: 7vh;
  width: 25vw;
  font-size: 1.5rem;
  border-radius: 5px;
  border: white;

}

.button {
  height: 7vh;
  width: 25vw;
  background-color: green;
  box-shadow: none;
  border-radius: 5px;
border: white;
font-size: 1.5rem;
}

.button:hover {
  color: rgb(0, 0, 0);
  height: 7vh;
  width: 25vw;
  font-size: 1.5rem;
  background-color: rgb(41, 156, 41);
}
