.dashboard-container {
    height: 100vh;
    width: 100%;
    /* background-color: black; */
    background-image: url(https://blogger.googleusercontent.com/img/b/R29vZ2xl/AVvXsEi1tTPma0krJzCe0QZaCqYNP75T5FPydHUfEkn3XDW7eMjSIsWzSAhkkMpEyYTrrmTTmwDls0SpfcuKRF2k-C3C1D42L6L86ngLcxMldefRif7tqAYWppRAo1n-xht3-7NF8NeT95XAJYlsJ4QdnRboHFWSY620y2vWZg7lNKKULRX826MGSRX-u9NQ_Eo/s1600-rw/boitcoin-background-4k.webp);
    background-repeat: no-repeat;
    background-size: cover;
}

.counter {
    height: 10vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
}

.dashboard-display {
    height: 90vh;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.dashboard-display-one {
    height: 90%;
    width: 60%;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.dashboard-display-two {
    height: 90%;
    width: 30%;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width: 768px) { /* Tablets and below */
    .dashboard-container {
        height: 100vh;
        width: 100%;
        /* background-color: black;
        /* background-image: url(https://blogger.googleusercontent.com/img/b/R29vZ2xl/AVvXsEi1tTPma0krJzCe0QZaCqYNP75T5FPydHUfEkn3XDW7eMjSIsWzSAhkkMpEyYTrrmTTmwDls0SpfcuKRF2k-C3C1D42L6L86ngLcxMldefRif7tqAYWppRAo1n-xht3-7NF8NeT95XAJYlsJ4QdnRboHFWSY620y2vWZg7lNKKULRX826MGSRX-u9NQ_Eo/s1600-rw/boitcoin-background-4k.webp);
        background-repeat: no-repeat;
        background-size: cover; */ 
        display: flex;
        flex-direction: column;
    }
    
    .counter {
        height: 5vh;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 3rem;
    }
    
    .dashboard-display {
        height: 95vh;
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
    }
    
    .dashboard-display-one {
        height: 50%;
        width: 100%;
        border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .dashboard-display-two {
        height: 50%;
        width: 30%;
        border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
}
