.card {
    height: 15%;
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3.5rem;
}

@media screen and (max-width: 768px) {
    .card {
        height: 15%;
        width: 250%;
        display: flex;
       flex-direction: row;
       font-size: 3rem;
    }
}