.panel {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    float: left;
    flex-wrap: wrap;
}

@media screen and (max-width: 768px) {
    .panel {
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: row;
        float: left;
        flex-wrap: wrap;
    }
      
}