.coin {
    height: 30%;
    width: 20%;
    background-color: aliceblue;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
    color: black;
    cursor: default;
    flex-wrap: wrap;
}

.coin-img {
    height: 100%;
    width: 100%;
    border-radius: 90%;
}

@media screen and (max-width: 768px) {
    .coin {
        height: 30%;
        width: 30%;
        background-color: aliceblue;
        border-radius: 50%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        font-size: 3rem;
        color: black;
        cursor: default;
        margin-left: 15px;
    }
    
    .coin-img {
        height: 100%;
        width: 100%;
        border-radius: 90%;
    }
}